import React, { useState, useEffect } from "react";
import Fade from "react-reveal/Fade";
import { Link, useHistory, useLocation } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import decode from 'jwt-decode';
import * as actionType from '../constants/actionTypes';

import Button from "elements/Button";
import BrandIcon from "parts/IconText";

export default function Header(props) {

  const [user, setUser] = useState(JSON.parse(localStorage.getItem('profile')));
  const dispatch = useDispatch();
  const location = useLocation();
  const history = useHistory();

  const logout = () => {
    dispatch({ type: actionType.LOGOUT });
    history.push('/');
    setUser(null);
    alert("Anda berhasil logout")
  };

  const login = () => {
    history.push('/auth');
  };

  useEffect(() => {
    const token = user?.token;
    if (token) {
      const decodedToken = decode(token);
      if (decodedToken.exp * 1000 < new Date().getTime()) logout();
    }
    setUser(JSON.parse(localStorage.getItem('profile')));
  }, [location]);


  const getNavLinkClass = (path) => {
    return props.location.pathname === path ? " active" : "";
  };

  if (props.isCentered)
    return (
      <Fade>
        <header className="spacing-sm">
          <div className="container">
            <nav className="navbar navbar-expand-lg navbar-light">
              <Button className="brand-text-icon mx-auto" href="" type="link">
                Sambas<span className="text-gray-900">Vacation.</span>
              </Button>
            </nav>
          </div>
        </header>
      </Fade>
    );

  return (
    <Fade>
      <header className="spacing-sm">
        <div className="container">
            {user?.result ? (
          <nav className="navbar navbar-expand-lg navbar-light">
            <BrandIcon />
            <div className="collapse navbar-collapse">
              <ul className="navbar-nav ml-auto">
              <li className={`nav-item${getNavLinkClass("/")}`}>
              <Button className="nav-link" type="link" href="/">
                Home
              </Button>
              </li>
                {/* <li className={`nav-item${getNavLinkClass("/browse-by")}`}>
                  <Button className="nav-link" type="link" href="/browse-by">
                    Browse By
                  </Button>
                </li> */}
                {/* <li className={`nav-item${getNavLinkClass("/stories")}`}>
                  <Button className="nav-link" type="link" href="/stories">
                    Stories
                  </Button>
                </li>
                <li className={`nav-item${getNavLinkClass("/agents")}`}>
                  <Button className="nav-link" type="link" href="/agents">
                    Agents
                  </Button>
                </li> */}
              <li className="nav-item">
                  <img className="img rounded mx-auto d-block" style={{height:40}} src={user?.result?.imageUrl || user?.result.file} onClick={logout}/> 
              </li>
              <li className="nav-item">
                  <span> Welcome, {user?.result.name} </span>
              </li>

              </ul>
            </div>
          </nav>
            ) : (
              <nav className="navbar navbar-expand-lg navbar-light">
                <BrandIcon />
            <div className="collapse navbar-collapse">
              <ul className="navbar-nav ml-auto">
                
              <li className={`nav-item${getNavLinkClass("/")}`}>
              <Button className="nav-link" type="link" href="/"> Home </Button>
            </li>
              <li className={`nav-item`}>
              <Button className="nav-link text-primary" type="link" onClick={login}> Login </Button>
            </li>
              </ul>
            </div>
          </nav>
            )}
        </div>
      </header>
    </Fade>
  );
}
